/**
 * External dependencies
 */
const { __ } = wp.i18n;
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Component, Fragment } = wp.element;
const {
    PanelBody,
    Placeholder,
    QueryControls,
    Spinner,
    SandBox
} = wp.components;
const {
    InspectorControls,
} = wp.editor;
const { withSelect } = wp.data;
const { createHooks } = wp.hooks;

class Testimonials extends Component {


    constructor() {
        super(...arguments);
    }


    render() {
        const { attributes, setAttributes, latestPosts, isSelected } = this.props;
        const { order, orderBy, postsToShow } = attributes;
        const hasPosts = Array.isArray(latestPosts) && latestPosts.length;
        const displayPosts = latestPosts;
        const hooks = createHooks();
        
        return (
            <Fragment>
                <InspectorControls>
                    <PanelBody title={__('Testimonials')}>
                        <QueryControls
                            {...{ order, orderBy }}
                            numberOfItems={postsToShow}
                            onOrderChange={(value) => setAttributes({ order: value })}
                            onOrderByChange={(value) => setAttributes({ orderBy: value })}
                            onNumberOfItemsChange={(value) => setAttributes({ postsToShow: value })}
                        />
                    </PanelBody>
                </InspectorControls>
                <section className="clients">
                    <div className="posts posts--client">
                        {hasPosts ? (displayPosts.map((post, i) =>
                            hooks.applyFilters('clients-block-editor', (
                                <article className="post-{post.id} client type-client status-{post.status} hentry">
                                    {post._embedded ? (
                                        <img src={post._embedded["wp:featuredmedia"][0].source_url} alt={post.title.rendered} />
                                    ) : (post.title.rendered)}
                                </article>
                            ), post, i)
                        )) : (
                            <Placeholder icon="admin-post" label={__('Clients')}>
                                {!Array.isArray(latestPosts) ?
                                    <Spinner /> :
                                    __('No clients found.')
                                }
                            </Placeholder>
                        )}
                    </div>
                </section>
            </Fragment>
        );
    }
}


export default withSelect((select, props) => {
    const { postsToShow, order, orderBy } = props.attributes;
    const { getEntityRecords } = select('core');
    const latestPostsQuery = {
        orderby: orderBy,
        order: order,
        per_page: postsToShow,
        _embed: true,
    };
    return {
        latestPosts: getEntityRecords('postType', 'client', latestPostsQuery),
    };
})(Testimonials);
